<template>
     <div class="public_content">
        <div class="public_title"><span>完善个人信息</span></div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm" v-if="dataSuccess">

            <el-form-item label="形象照片" prop="head_portrait">
               <upload-img :data='ruleForm.head_portrait' @onSuccess="changeLogo($event,'head_portrait')"></upload-img>
            </el-form-item>

            <el-form-item label="微信二维码" prop="wechat">
                <upload-img :data='ruleForm.wechat' @onSuccess="changeLogo($event,'wechat')"></upload-img>
            </el-form-item>
 


            <el-form-item label="真实姓名" prop="realname">
                <el-input v-model="ruleForm.realname" style="width: 120px"></el-input>
            </el-form-item>

            <el-form-item label="手机号码" prop="phone">
                <el-input v-model="ruleForm.phone" style="width: 120px"></el-input>
            </el-form-item>

             <el-form-item label="身份证号" prop="id_card">
                <el-input v-model="ruleForm.id_card" style="width: 240px"></el-input>
            </el-form-item>

            <el-form-item label="身份证正反面" prop="card_view">
               <upload-img width="250px" height="180px" class="inlineBlock" style="margin-right: 20px;" :data='ruleForm.card_view' @onSuccess="changeLogo($event,'card_view')"  bg='idcardFont' />
               <upload-img  width="250px" height="180px" class="inlineBlock" :data='ruleForm.card_opposite' @onSuccess="changeLogo($event,'card_opposite')"  bg='idcardBack' />
            </el-form-item> 

            <el-form-item label="手持身份证正面照" prop="hand_card">
               <upload-img width="250px" height="180px" :data='ruleForm.hand_card' @onSuccess="changeLogo($event,'hand_card')" bg='idcrad'></upload-img>
            </el-form-item>

            <el-form-item label="资质证数照" prop="qualification" style="margin-bottom: 120px;">
               <upload-img  width="250px" height="180px" class="inlineBlock" :data='ruleForm.qualification' @onSuccess="changeLogo($event,'qualification')" bg='aptitude'></upload-img>
                <span class="public_littleTip"><i class="iconfont">&#xe670;</i>多图上传，最多传3张</span>

            </el-form-item>

            <el-button type="primary" round @click="submitForm('ruleForm')" class="public_submit">确认提交</el-button>
         </el-form>
    </div>
</template>

<script>
import {getAgentInfo,perfectInfo} from '@/api/login'
import uploadImg from "@/components/upload/uploadimg";

export default {
    components: {
    uploadImg, 
  },
  data() {
    return { 
      dataSuccess: false,  // 触发子组件
      ruleForm: {
        head_portrait: "",
        wechat: "",
        realname: "",
        id_card: "",
        card_opposite: "",
        card_view: "",
        hand_card: "",
        qualification: "", 
      },
      rules: {
        head_portrait: [{ required: true, message: "请上传门店logo" }],
        realname: [
          { required: true, message: "请输入门店名称", trigger: "change" }
        ],
        phone: [{ required: true, message: "请输入门店电话", trigger: "change" }]
      }
    };
  },
  created () {
    getAgentInfo().then(res => {
      this.dataSuccess = true
      if (res.data.code == 1) { 
        this.ruleForm = res.data.data 
      }
    })
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          perfectInfo(this.ruleForm).then(res => {
            if (res.data.code == 1) {
              this.$message.success("编辑成功")
            }
          }) 
        } else {
          this.$message.error("请填写完整信息")
          return false;
        }
      });
    }, 
    changeLogo(data,key) { 
      this.ruleForm[key] = data;
    }
  }
};
</script>

<style>
</style>



